import { useEffect } from 'react';
import { preloadImages } from '../utils/imagePreloader';

//     
const criticalImages = [
  //  
  '/images/background/background(1).png',
  '/images/background/background(2).png',
  '/images/background/uzbek-pattern.jpg',
  
  //     About
  '/images/sertificate/ChatGPT Image 6 . 2025 ., 17_53_15.png',
  '/images/about/premium/DSC01398.jpg',
  '/images/about/vip/DSC01398.jpg',
  '/images/about/obshiy/DSC01421.jpg',
  '/images/about/kabinka/DSC01417.jpg',
  '/images/about/kabinka/DSC01427.jpg',
  
  //   
  '/images/sayqal-logo.png'
];

/**
 *      
 *      
 */
const ImagePreloader = () => {
  useEffect(() => {
    //      
    const preloadCriticalImages = async () => {
      try {
        //  setTimeout     
        setTimeout(() => {
          preloadImages(criticalImages)
            .then(() => console.log('Critical images preloaded'))
            .catch(err => console.warn('Error preloading images:', err));
        }, 1000); //    1   
      } catch (error) {
        console.warn('Error in image preloader:', error);
      }
    };

    preloadCriticalImages();
  }, []);

  //     
  return null;
};

export default ImagePreloader; 